export default {

  name: "FAQ",

  components: {},

  data: () => ({
    aboutFAQdialog: false,
    panel: [0,1,2,3,4,5,],
    readonly: false,
  }),

  computed: {},

  methods: {
    // Create an array the length of our items
    // with all values as true
    all() {
      this.panel = [...Array(this.items).keys()].map((k, i) => i)
    },
    // Reset the panel
    none() {
      this.panel = []
    },
  },

  watch: {},
